import React from 'react';
import PropTypes from 'prop-types'
import {styleSheetJson} from "../react-app-env";
import {Route, Switch} from "react-router";
const Home = React.lazy(() => import("./home"));

export default class Index extends React.Component {
    static propTypes = {
        children: PropTypes.element
    };

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        return (
            <div style={styleSheet.home}>
                <p style={styleSheet.title}>智能化能耗管理系统</p>
                <div style={styleSheet.main}>
                    <Switch>
                        <Route path='/home'>
                            <React.Suspense fallback={<div>加载中</div>}>
                                <Home/>
                            </React.Suspense>
                        </Route>
                    </Switch>
                </div>
            </div>
        )
    }
}

export interface desktopHome {
    children: React.ReactElement
}

const styleSheet: styleSheetJson = {
    home: {
        width: '100%',
        height: '100%',
        backgroundImage: `url(${require("../resource/desktop/mainBackground.png")})`,
        backgroundRepeat: 'no-repeat',
        display: 'flex',
        paddingBottom: 35,
        flexDirection: 'column'
    },
    title: {
        color: '#FFF',
        lineHeight: '110px',
        fontSize: 26,
        textAlign: 'center'
    },
    main: {
        backgroundImage: `url(${require("../resource/desktop/mainBox.png")})`,
        backgroundSize: '100% 100%',
        backgroundRepeat: 'no-repeat',
        backgroundClip: "content-box",
        marginLeft: 40,
        marginRight: 40,
        flexGrow: 1,
        height: "100%"
    }
};
